@import 'variables';
@import 'stars';

.cards {
  display: flex;
  flex-wrap: wrap;

  box-sizing: border-box;
  width: 1024px;
  margin: 0 auto;
  padding: 50px 40px;
}

.card {
  &:hover {
    transform: scale(1.2);

    &__title {
      &:hover {
        color: #34568b;
      }
    }
  }

  box-sizing: border-box;
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  background: #fff;
  font-family: $font;
  transition: transform 0.3s ease;

  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    margin: 0 0 4px;
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    color: $text-accent-color;
  }

  &__code {
    margin-bottom: 16px;
    font-size: 10px;
    line-height: 14px;
    color: $text-secondary-color;
  }

  &__review-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;
  }

  &__review-count {
    color: $text-accent-color;
    font-size: 10px;
    line-height: 14px;
  }

  &__price-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price-label {
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
    color: $text-secondary-color;
  }

  &__price {
    font-size: 16px;
    line-height: 18px;
    font-weight: bold;
    color: $text-accent-color;
  }

  &__buy {
    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;
    height: 40px;
    border: 1px solid $main-color;
    border-radius: 5px;

    font-size: 14px;
    line-height: 16px;
    color: #fff;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;

    background-color: $main-color;

    &:hover {
      color: $main-color;
      background-color: #fff;
    }
  }
}
